<template>
    <div class="lightbox-cpn">
        <div v-show="show_big" ref="lb_modal" class="lightbox-modal">
            <div v-if="current_family != null">
                <div class="ctn-btn">
                    <button v-if="current_family.cover" type="button" class="lightbox-modal-btn lightbox-modal-cover" aria-label="Cover" @click="cover">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-aspect-ratio" viewBox="0 0 16 16">
                        <path d="M0 3.5A1.5 1.5 0 0 1 1.5 2h13A1.5 1.5 0 0 1 16 3.5v9a1.5 1.5 0 0 1-1.5 1.5h-13A1.5 1.5 0 0 1 0 12.5v-9zM1.5 3a.5.5 0 0 0-.5.5v9a.5.5 0 0 0 .5.5h13a.5.5 0 0 0 .5-.5v-9a.5.5 0 0 0-.5-.5h-13z"/>
                        <path d="M2 4.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1H3v2.5a.5.5 0 0 1-1 0v-3zm12 7a.5.5 0 0 1-.5.5h-3a.5.5 0 0 1 0-1H13V8.5a.5.5 0 0 1 1 0v3z"/>
                        </svg>
                    </button>
                    <button v-if="current_family.img_list.length > 1 && current_family.carousel" type="button" class="lightbox-modal-btn lightbox-modal-reload" aria-label="Cycle" @click="cycle(true)">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-arrow-clockwise" viewBox="0 0 16 16">
                        <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                        <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                        </svg>
                    </button>
                    <button v-if="current_family.fullscreen" type="button" class="lightbox-modal-btn lightbox-modal-fs" aria-label="Fullscreen" @click="fullScreen">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-arrows-fullscreen" viewBox="0 0 16 16">
                        <path fill-rule="evenodd" d="M5.828 10.172a.5.5 0 0 0-.707 0l-4.096 4.096V11.5a.5.5 0 0 0-1 0v3.975a.5.5 0 0 0 .5.5H4.5a.5.5 0 0 0 0-1H1.732l4.096-4.096a.5.5 0 0 0 0-.707zm4.344 0a.5.5 0 0 1 .707 0l4.096 4.096V11.5a.5.5 0 1 1 1 0v3.975a.5.5 0 0 1-.5.5H11.5a.5.5 0 0 1 0-1h2.768l-4.096-4.096a.5.5 0 0 1 0-.707zm0-4.344a.5.5 0 0 0 .707 0l4.096-4.096V4.5a.5.5 0 1 0 1 0V.525a.5.5 0 0 0-.5-.5H11.5a.5.5 0 0 0 0 1h2.768l-4.096 4.096a.5.5 0 0 0 0 .707zm-4.344 0a.5.5 0 0 1-.707 0L1.025 1.732V4.5a.5.5 0 0 1-1 0V.525a.5.5 0 0 1 .5-.5H4.5a.5.5 0 0 1 0 1H1.732l4.096 4.096a.5.5 0 0 1 0 .707z"/>
                        </svg>
                    </button>
                    <button type="button" class="lightbox-modal-btn lightbox-modal-close" aria-label="Close" @click="hideImg">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-x-lg" viewBox="0 0 16 16">
                        <path d="M2.146 2.854a.5.5 0 1 1 .708-.708L8 7.293l5.146-5.147a.5.5 0 0 1 .708.708L8.707 8l5.147 5.146a.5.5 0 0 1-.708.708L8 8.707l-5.146 5.147a.5.5 0 0 1-.708-.708L7.293 8 2.146 2.854Z"/>
                        </svg>
                    </button>
                </div>

                <button v-if="current_family.img_list.length > 1" @click="prev" class="lightbox-modal-btn lightbox-modal-prev">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-chevron-left" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z"/>
                    </svg>
                </button>
                <button v-if="current_family.img_list.length > 1" @click="next" class="lightbox-modal-btn lightbox-modal-next">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-chevron-right" viewBox="0 0 16 16">
                    <path fill-rule="evenodd" d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </button>

                <figure class="lightbox-modal-img" @click="hideImg">
                    <transition :name="transition_name">
                        <img v-if="img.src != null" src="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==" :style="`width:100%; height:100%;background-repeat:no-repeat;background-position:center;background-image:url(${img.src}); background-size:${img.cover ? 'cover' : 'contain'}`">
                    </transition>
                    <figcaption v-if="img.caption.length > 0">{{ img.caption }}</figcaption>
                </figure>

            </div>
        </div>

        <figure v-show="!show_big" class="lightbox-thumb" @click="showImg" :data-imgview-family="img.family">
            <img :src="img.thumb" :alt="img.alt" :wdith="img.width" :height="img.height" :class="img.classname" />
            <figcaption v-show="caption.length > 0">{{ caption }}</figcaption>
        </figure>
    </div>
</template>

<script>
export default {
    props:{
        src: {type: String, required: true},
        thumb: {type: String, required: false},
        alt: {type: String, default: "alt"},
        family: {type: String, default: "lightbox"},
        width: {type: [String, Number], default: 100},
        height: {type: [String, Number], default: 100},
        classname: {type: [String, Number], default: ""},
        caption: {type: [String], default: ""},
        option: {type: [Object], default: {
                    cover: false,
                    fullscreen: true,
                    carousel: true,
                    interval: 10000
                }
        },
    },
    data() {
        return {
            img:{
                src: "",
                thumb: '',
                alt:"",
                width: 100,
                height: 100,
                classname: '',
                family: 'lightbox',
                caption: '',
                option:{
                    cover: false,
                    fullscreen: true,
                    carousel:{
                        enabled: true,
                        interval: 10000
                    }
                }
            },
            show_big: false,
            old_overflow: 'auto',
            images: {},
            idx: -1,
            timer: null,
            current_family: null,
            default_option: {
                cover: false,
                fullscreen: true,
                carousel: true,
                interval: 10000
            },
            transition_name: "slideRight"
        }
    },
    watch: {
        src: {
            handler(new_src) {
                this.img.src =  new_src;
            },
            immediate: true
        },
        thumb: {
            handler(new_thumb) {
                this.img.thumb =  new_thumb;
            },
            immediate: true
        },
        alt: {
            handler(new_alt) {
                this.img.alt =  new_alt;
            },
            immediate: true
        },
        width: {
            handler(new_width) {
                this.img.width =  new_width;
            },
            immediate: true
        },
        height: {
            handler(new_height) {
                this.img.height =  new_height;
            },
            immediate: true
        },
        classname: {
            handler(new_classname) {
                this.img.classname =  new_classname;
            },
            immediate: true
        },
        family: {
            handler(new_family) {
                this.img.family =  new_family;
            },
            immediate: true
        },
        caption: {
            handler(new_caption) {
                this.img.caption =  new_caption;
            },
            immediate: true
        }
    },
    methods: {
        showImg(){
            this.show_big = true;
            this.old_overflow = document.getElementsByTagName('body')[0].style.overflow;
            document.getElementsByTagName('body')[0].style.overflow = "hidden";

            this.current_family = window.lightbox.family.find(el => el.name == this.family);
            this.idx = this.getCurrentIdx();
            this.changeImage();
            this.cycle();
        },
        hideImg(){
            document.getElementsByTagName('body')[0].style.overflow = this.old_overflow;
            if(this.timer){
                clearTimeout(this.timer);
                this.timer = null;
            }
            this.GoOutFullscreen();
            this.show_big = false;
        },
        prev(){
            this.idx = this.idx > 0 ? this.idx -1 : this.current_family.img_list.length -1;
            this.transition_name = 'slideLeft';
            this.changeImage();
        },
        next(){
            this.idx = this.idx  < (this.current_family.img_list.length -1) ? this.idx + 1 : 0;
            this.transition_name = 'slideRight';
            this.changeImage();
        },
        changeImage(){
            this.img.src = null;
            this.$forceUpdate();
            setTimeout(() => {
                this.img.src = this.current_family.img_list[this.idx].src;
                this.img.caption = this.current_family.img_list[this.idx].caption;
            }, 100)
        },
        getCurrentIdx(){
            return this.current_family.img_list.findIndex(el => el.src == this.src);
        },
        cover(){
            this.img.cover = ! this.img.cover;
        },
        cycle(click = false){
            if(this.current_family.img_list.length > 1 && this.current_family.carousel){
                if(this.timer == null){
                    if(click){
                        this.next();
                    }
                    this.timer = setInterval(() => {
                        this.next();
                    }, this.current_family.interval);
                }else{
                    clearTimeout(this.timer);
                    this.timer = null;
                }
            }
        },
        fullScreen(){
            if(this.IsFullScreenCurrently()){
                this.GoOutFullscreen();
            }else{
                this.GoInFullscreen();
            }
        },
        GoInFullscreen() {
            const element = this.$refs.lb_modal;
            if(element.requestFullscreen)
                element.requestFullscreen();
            else if(element.mozRequestFullScreen)
                element.mozRequestFullScreen();
            else if(element.webkitRequestFullscreen)
                element.webkitRequestFullscreen();
            else if(element.msRequestFullscreen)
                element.msRequestFullscreen();
        },
        GoOutFullscreen() {
            try{
                if(document.exitFullscreen)
                    window.exitFullscreen();
                else if(document.mozCancelFullScreen)
                    document.mozCancelFullScreen();
                else if(document.webkitExitFullscreen)
                    document.webkitExitFullscreen();
                else if(document.msExitFullscreen)
                    document.msExitFullscreen();
            }catch(e){}
        },
        IsFullScreenCurrently() {
            const full_screen_element = document.fullscreenElement || document.webkitFullscreenElement || document.mozFullScreenElement || document.msFullscreenElement || null;
            if(full_screen_element === null){
                return false;
            }else{
                return true;
            }
        }
    },
    mounted(){
        if(typeof this.thumb == "undefined" || this.thumb.length === 0){
            this.img.thumb = this.src;
        }

        Object.assign(this.default_option, this.option);

        if(typeof window.lightbox == "undefined"){
            window.lightbox = {};
            window.lightbox.family = [];
        }
        const index = window.lightbox.family.findIndex(el => {return el.name == this.family});
        if(index < 0){
            window.lightbox.family.push( {
                name: this.family,
                fullscreen: this.default_option.fullscreen,
                cover: this.default_option.cover,
                carousel: this.default_option.carousel,
                interval: this.default_option.interval,
                img_list: [{src: this.src, caption: this.caption}]
            })
        }else{
            window.lightbox.family[index].img_list.push({src: this.src, caption: this.caption});
        }
    }
}
</script>

<style scoped>

